.spinner-wrapper {
    height: 60vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .spinner {
        display: inline-block;
        width: 50px;
        height: 50px;
        border: 3px solid rgba(195, 195, 195, 0.6);
        border-radius: 50%;
        border-top-color: #636767;
        animation: load 1s ease-in-out infinite;
    }
 }

@keyframes load {
    to {
      -webkit-transform: rotate(360deg);
    }
  }
  @-webkit-keyframes load {
    to {
      -webkit-transform: rotate(360deg);
    }
  }